import axios from 'axios';
import LogUtils from './../logUtils/index';
require('promise.prototype.finally').shim();
/**
 * 
 * axios
 */
export default class HttpRequestUtils {
    /**
     * @description axios axios
     * @param fn
     * axios HttpRequestUtils class
     * @return { class } HttpRequestUtils 
     */
    static init(fn) {
        LogUtils.logInfo('https://github.com/IFmiss/d-utils/blob/typescript/src/lib/httpRequestUtils/axiosConfig.ts', `[d-utils] http_request HttpRequestUtils.init aixos => `);
        if (fn && typeof fn === 'function') {
            fn.call(null, axios, HttpRequestUtils);
        }
        HttpRequestUtils.isInit = true;
        return HttpRequestUtils;
    }
    /**
     * @description get
     * @param { string } url url
     * @param { object } params 
     * @param { object } config axios
     * @return { Promise }
     */
    static get(url, params, config) {
        if (!HttpRequestUtils.isInit) {
            LogUtils.logError('HttpRequestUtils.isInit()', `[d-utils] http_request: => error`);
            return;
        }
        const getInfo = Object.assign({}, { params }, config);
        axios.get(url, getInfo).then((res) => {
            LogUtils.groupCollapsed(`[d-utils] http_request get  => ${url}`, LogUtils.successColor);
            LogUtils.logInfo(res, `http_request response => `);
            LogUtils.groupEnd();
            return Promise.resolve(res);
        }).catch((e) => {
            LogUtils.groupCollapsed(`[d-utils] http_request get  ${url}`, LogUtils.successColor);
            LogUtils.logInfo(e, `http_request error => `);
            LogUtils.groupEnd();
            return Promise.reject(e);
        });
    }
    /**
     * @description post
     * @param { string } url url
     * @param { object } data 
     * @param { object } config axios
     * @return { Promise }
     */
    static post(url, data, config) {
        if (!HttpRequestUtils.isInit) {
            LogUtils.logError('HttpRequestUtils.isInit()', `[d-utils] http_request error => `);
            return;
        }
        const postInfo = Object.assign({}, { data: data }, config);
        axios.post(url, postInfo).then((res) => {
            LogUtils.groupCollapsed(`[d-utils] http_request post  => ${url}`, LogUtils.successColor);
            LogUtils.logInfo(res, `http_request response => `);
            LogUtils.groupEnd();
            return Promise.resolve(res);
        }).catch((e) => {
            LogUtils.groupCollapsed(`[d-utils] http_request post  => ${url}`, LogUtils.errorColor);
            LogUtils.logInfo(e, `http_request error => `);
            LogUtils.groupEnd();
            return Promise.reject(e);
        });
    }
}
/**
 * CODE
 */
HttpRequestUtils.successCode = 200;
/**
 * url
 */
HttpRequestUtils.baseUrl = '';
/**
 * timeout
 */
HttpRequestUtils.timeOut = 15000;
/**
 * 
 */
HttpRequestUtils.isInit = false;
