import LogUtils from './../logUtils/index';
export default class PromiseUtils {
    /**
     * @description 
     * @param { funciton } callback while  boolean  true  
     * @param { number } loopTime   100
     * @param { number } timeout    10000  10
     * @return Promise
     */
    static wait(callback, loopTime = 100, timeout = 10000) {
        return new Promise((resolve, reject) => {
            if (typeof callback === 'function' && typeof callback() === 'boolean') {
                const t = setInterval(() => {
                    if (callback()) {
                        clearTimeout(t);
                        clearTimeout(out);
                        resolve();
                    }
                }, loopTime);
                const out = setTimeout(() => {
                    LogUtils.logError('', '[d-utils] PromiseUtils wait timeout');
                    clearTimeout(t);
                    clearTimeout(out);
                    reject();
                }, timeout);
            }
        });
    }
    /**
     * @description 
     * @param { promise[] } requestQueues   promise
     */
    static requestOnLoad(requestQueues) {
        return Promise.all(requestQueues);
    }
    /**
     * @description 
     * @param { number } timer    
     * @return promise
     */
    static sleep(timer) {
        return new Promise((resolve) => {
            setTimeout(() => {
                resolve();
            }, timer);
        });
    }
}
